export default function once(emitter, event, options = {}) {
  return new Promise(function(resolve, reject) {
    if (options.array) {
      emitter.once(event, function(...args) {
        resolve(args);
      });
    }
    else {
      emitter.once(event, resolve);
    }
    if (!options.ignoreErrors) {
      emitter.once('error', reject);
    }
  });
};
